<template>
	<view>
		<view class="content">
			<u-toast ref="uToast"></u-toast>
			<!-- 标题 -->
			<view class="content-text">
				<view class="content-text-title">欢迎回来！</view>
				<view class="content-text-sketch">请你相信，你的坚持终将美好，加油。</view>
			</view>
			<!-- 表单 start-->
			<view class="content-from">
				<view class="cu-form-group">
					<u-icon name="man-add" color="#409EFF" size="28"></u-icon>
					<u--input placeholder="*请输入手机号" shape="circle" clearable border="none" v-model="InfoSubmit.mobile">
					</u--input>
				</view>
				<view class="cu-form-group">
					<u-icon name="eye" color="#409EFF" size="28"></u-icon>
					<u--input placeholder="*请输入密码" type="password" shape="circle" clearable border="none"
						v-model="InfoSubmit.password"></u--input>
				</view>
				<view class="content-btn">
					<u-button type="primary" shape="circle" text="登录" @click="LoginSubmit"></u-button>
					<view class="forget-regist">
						<view>忘记密码？</view>
						<view @click="regist()">注册账户？</view>
					</view>
				</view>
			</view>

		</view>

	</view>
</template>

<script>
	var app=getApp();
	export default {
		data() {
			return {
				InfoSubmit: {
					"mobile": null,
					"password": null,
				}
			}
		},
		methods: {
			LoginSubmit() {
				this.$request.request("/Login/login", this.InfoSubmit, "POST").then(res => {
					if (res.code == 200) {
						uni.setStorageSync('token',res.token);
						uni.setStorage({key:'userInfo',data:res.data});
						this.$refs.uToast.show({
							message: res.msg,
							complete() {
								uni.navigateTo({
									url: "/pages/index/index"
								})
							}
						})
					} else {
						this.$refs.uToast.show({
							message: res.msg
						})
					}
				}).catch()
			},
			regist() {
				uni.navigateTo({
					url: '/pages/register/register'
				})
			}
		}

	}
</script>

<style>
	.content {
		width: 100%;
		margin: 0 auto;
		box-sizing: border-box;
		padding: 20% 5%;
		height: 100vh;
		/* background: url("https://image.meiye.art/Fkh6IwqdV-OwVLD2WZrhp5VrqD3G"); */
		/* background-size: cover; */
	}
	.content-text {
		padding: 15% 0px;
	}

	.content-text-title {
		font-size: 50rpx;
		font-weight: bold;
		letter-spacing: 10rpx;
	}

	.content-text-sketch {
		margin-top: 20rpx;
		letter-spacing: 5rpx;
	}

	.cu-form-group {
		display: flex;
		justify-content: flex-start;
		margin-top: 15px;
		border-radius: 20px;
		height: 45px;
		background: #f9f9f9;
		box-shadow: inset 5px 5px 10px #dedede,
			inset -5px -5px -10px #ffffff;
	}

	.cu-form-group .u-icon {
		margin: 0px 10px;
	}

	.content-btn {
		margin-top: 100rpx;
	}

	.content-btn button {
		letter-spacing: 10rpx;
		margin-right: 2%;
		background-color: #409EFF;
		border: none;
	}

	.forget-regist {
		width: 95%;
		margin-left: 2.5%;
		margin-top: 15px;
		display: flex;
		justify-content: space-between;
		color: #B9B9B9;
	}

	.forget-regist view:nth-child(2) {
		color: #409EFF;
	}

	/*  */
	.login-other {
		margin-top: 100rpx;
	}

	.login-other-text {
		text-align: center;
		letter-spacing: 6rpx;
		color: #B9B9B9;
	}

	.login-other-icon {
		margin-top: 40rpx;
		display: flex;
		justify-content: center;
	}

	.login-other-icon .u-icon {
		font-size: 60rpx;
		margin-right: 30rpx;
	}

	.u-input {
		padding-right: 10px !important;
	}
</style>
